html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  background-color: #000;
  display: flex;
  align-items: flex-start;

  .info {
    color: #fff;
    flex: 1;
    align-self: stretch;
    overflow-y: auto;
  }
}
